import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl'
import bear from './Pictures/bear.JPG'
import samueli from './Pictures/samueli.JPG'
import fockey from './Pictures/fockey.JPG'
import Resume1 from './Pictures/SarahMauricioResume.pdf'
import bear2 from './Pictures/bear2.jpg'

class About extends Component{
    render(){
        return (
            <div style = {{width: '100%', margin: 'auto'}}>
                <div style={{width: '80%', margin: 'auto'}}>
                        <Grid>
                        <Cell col={3}>
                                <img className = "about-pics" src= {bear2} alt = "bear with me"></img>
                            </Cell>
                            <Cell col={3}>
                                <img className = "about-pics" src= {samueli} alt = "henry samueli sign"></img>
                            </Cell>
                            <Cell col={3}>
                                <img className = "about-pics" src= {fockey} alt = "field hockey group"></img>
                            </Cell>
                            <Cell col={3}>
                                <img className = "about-pics" src= {bear} alt = "bear with me"></img>
                            </Cell>
                        </Grid>
                        <h2 style = {{fontSize: '3vw',textAlign: 'center'}}>Hello, World ! My name is Sarah Mauricio.</h2>
                    </div>
                <Grid className="about-grid">
                    <Cell col={12}>
                        <div className = "grid-text">
                            <p style = {{fontSize: '1.8vw', lineHeight: '2.5vw'}}>
                                public class Sarah_Mauricio {'{'}<br/><br/>
                                    {'\u00A0'} {'\u00A0'} String hometown = "San Diego, CA" ; <br/><br/>
                                    
                                    {'\u00A0'} {'\u00A0'} String university = "University of California, Los Angeles" ; <br/>
                                    {'\u00A0'} {'\u00A0'} String major = "Computer Science and Engineering" ; <br/>
                                    {'\u00A0'} {'\u00A0'} String gradDate = "June 2023" ;<br/>
                                    {'\u00A0'} {'\u00A0'} double gpa = 4.0 ;<br/><br/>

                                    {'\u00A0'} {'\u00A0'} String [ ] compSciInterests = {'{'} "Front-End Development", "Mobile Development" {'}'} ; <br/><br/>

                                    {'\u00A0'} {'\u00A0'} String [ ] hobbies = {'{'} "Soccer", "Field Hockey", "Hiking", "Baking", "Drawing" {'}'} ; <br/><br/>

                                    {'\u00A0'} {'\u00A0'} class LatestWorkExperience {'{'}<br/>
                                        {'\u00A0'} {'\u00A0'} {'\u00A0'} {'\u00A0'} String company = "TechKnowHow" ; <br/>
                                        {'\u00A0'} {'\u00A0'} {'\u00A0'} {'\u00A0'} String position = "Lead Instructor" ; <br/>
                                        {'\u00A0'} {'\u00A0'} {'\u00A0'} {'\u00A0'} String startDate = "June 2020" ; <br/>
                                        {'\u00A0'} {'\u00A0'} {'\u00A0'} {'\u00A0'} String endDate = "Present" ; <br/>
                                        {'\u00A0'} {'\u00A0'} {'}'} <br/><br/>

                                    {'\u00A0'} {'\u00A0'} String email = "sarahmauricio2019@gmail.com" ; <br/><br/>
                                    {'\u00A0'} {'\u00A0'} String resumeLink = <a href={Resume1}>"SarahMauricioResume.pdf" ;</a> <br/><br/>
                                    
                                    {'}'}
                            </p>
                        </div>
                    </Cell>
                </Grid>
                <h3 style = {{width: '80%', margin: 'auto', fontSize: '2.5vw'}}> Here's a little more about me: </h3> <br/>
                <h6 style = {{width: '80%', margin: 'auto', fontSize: '1.5vw'}}>
                    I first got into Computer Science in high school, where I took two AP Computer Science classes. I liked these classes, but at the time, I wasn't sure if 
                    I could do it for the rest of my life. When deciding which college I would attend, I was torn between Rice University and UCLA. I had thought I was majoring 
                    Civil Engineering at both schools, so I was having a hard time deciding which one I liked better. I ended up choosing UCLA because of the amazing opportunities 
                    I was presented with, like CEED. Only later on did I find out that my major was Computer Science. After figuring this out, I was sure that I wanted to change 
                    majors, but I decided to just try it out for a quarter and see how I liked it. This was one of the best decisions I have made. I love Computer Science at UCLA 
                    and wouldn't trade my experience for the world. <br/><br/>

                    I have enjoyed exploring the different fields of Computer Science this past year. I want to continue exploring Front-End Development and Mobile Technologies. 
                    They are both really fascinating to me. I plan to build a more advanced mobile app this year to expand my knowledge in this part of Software Engineering.<br/><br/>

                    I am eager to learn new things. I was interested in learning more about web development, so I joined the SOLES dev team at UCLA, despite not knowing 
                    much about React, which the team were using to develop the website. I then took it upon myself to complete tutorials and watch videos until I was confident designing 
                    pages on my own. I have now successfully created multiple pages for the website from scratch. I am also an effective communicator. I am currently teaching 8-15 year
                     olds the basics of Python as the Lead Instructor at TechKnowHow. I explain concepts clearly and thoroughly to make sure all students are 
                     able to grasp concepts they may have never seen before, so they will leave the camp with a greater understanding of Python. This has been such a rewarding experience 
                     for me because I love sharing my passion for computer science with younger students and hopefully motivating them to continue exploring the possibilites in 
                     Computer Science.<br/><br/><br/>
                </h6>
            </div>        
        );
    }
}

export default About