import React, {Component} from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, CardMenu, Button, IconButton} from 'react-mdl';
import ReactPlayer from "react-player";

import IOT from './Pictures/IOT.jpg'
import IOT2 from './Pictures/IOT2.jpg'
import IOT3 from './Pictures/IOT3.jpg'
import AI from './Pictures/AI.jpg'
import AI2 from './Pictures/AI2.jpg'
import AI3 from './Pictures/AI3.jpg'
import P1 from './Pictures/96P1.png'
import P2 from './Pictures/96P2.png'

class Projects extends Component{
    constructor(props){
        super(props);
        this.state = {activeTab:0};
    }

    toggleCategories(){
        if(this.state.activeTab === 1){
            return(
                <div className = "projects-grid">
                    <Card shadow={5} style = {{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style = {{color: '#fff', height: '200px', fontWeight: 'bold', background: 'url(data:image/png;base64,) center/cover'}}>
                        </CardTitle>
                        <CardText>
                            <h5 style = {{color: 'black', fontWeight: 'bold'}}>ToDo App</h5>
                            This is a simple todo app for Android. You are able to add items, remove items, and edit items.
                            <br/>
                            <br/>
                            Technologies: Java
                        </CardText>
                        <CardActions border>
                            <Button colored href="https://github.com/sarahmauricio/todo" rel="noopener noreferrer" target = "_blank">GitHub</Button>
                            <Button colored href="https://github.com/sarahmauricio/todo/blob/master/walkthroughWithUI.gif" rel="noopener noreferrer" target = "_blank">Live Demo</Button>
                        </CardActions>
                        <CardMenu style = {{color: '#fff'}}>
                            <IconButton name = "share"/>
                        </CardMenu>
                    </Card>
                    
                </div>
            )
        }else if(this.state.activeTab === 0){
            return(
                <div className="research">
                    <h1>Artificial Intelligence Research</h1>
                    <div style={{width: '80%', margin: 'auto'}}>
                        <Grid>
                            <Cell col={4}>
                                <img className = "research-pics" src= {AI2} alt = "AI pic"></img>
                            </Cell>
                            <Cell col={4}>
                                <img className = "research-pics" src= {AI} alt = "AI pic"></img>
                            </Cell>
                            <Cell col={4}>
                                <img className = "research-pics" src= {AI3} alt = "AI pic"></img>
                            </Cell>
                        </Grid>
                        <h3>AI Arena Research Team</h3>
                        <p>For this research project, I was introduced to engineering design and artificial 
                            intelligence fundamentals, in context of developing fully autonomous team of agents 
                            to face off in tournament setting. Additionally, I was exposed to concepts such as 
                            search and planning, neural networks, deep learning, clustering, evolutionary algorithms, 
                            and reinforcement learning. I then went on to design and implement intelligent strategies 
                            for a game of PacMan using Python. I implemented my own minimax, expectimax, and reward 
                            functions so that PacMan could optimize the amount of food he picked up. For the final project, 
                            I added different features to my game of PacMan, so it could beat the other PacMan team.
                            <br/><br/>
                            Technologies: Python
                        </p>
                        <p style = {{textAlign:'center'}}>Here is a video of my PacMan app after I added the Minimax, ExpectiMax, and Reward functions</p>
                        <ReactPlayer className = "research-videos"
                            url="https://youtu.be/FWlkkY0e-Dw"
                        />
                    </div>

                    <hr/>

                    <h1>Internet of Things Research</h1>  
                    <div style={{width: '80%', margin: 'auto'}}>
                        <Grid>
                            <Cell col={4}>
                                <img className = "research-pics" src= {IOT} alt = "IOT pic"></img>
                            </Cell>
                            <Cell col={4}>
                                <img className = "research-pics" src= {IOT3} alt = "IOT pic"></img>
                            </Cell>
                            <Cell col={4}>
                                <img className = "research-pics" src= {IOT2} alt = "IOT pic"></img>
                            </Cell>
                        </Grid>
                        <h3>SensorTile Research Team</h3>
                        <p>For this research project, I started by learning the principles of EmbeddedML and then using 
                            this knowledge to accomplish tasks such as having the SensorTile recognize an acceleration 
                            motion or being flipped. For the final project, my team and I implemented two gyroscope motions 
                            to train the SensorTile. We acted as if the SensorTile was a key. The first motion was turning 
                            the “key” to open a door and the second one was turning the “key” to the unlock position and then 
                            turning it back to lock it. These motions were done by both rotating the key to the right and to 
                            the left. With this device a contactless, gesture recognizing digital key can be created for convenience 
                            and longevity.
                            <br/><br/>
                            Technologies: C
                        </p>
                        
                            <p style={{textAlign:'center'}}>Here is a video of me training the SensorTile to recognize motions and then doing some test motions.</p>
                             <ReactPlayer className = "research-videos"
                                url="https://www.youtube.com/embed/TYbe3jmwtHQ"
                            /> 
                            <p style={{textAlign:'center', marginTop: '20px'}}>This is the code output that corresponds to the video.</p>
                            <img className = "research-pics2" src= {P1} alt = "sensorTile output"></img>
                            <img className = "research-pics2" src= {P2} alt = "sensorTile output"></img>
                            
                    </div>
                  
                </div>
            )
        }else if(this.state.activeTab === 2){
            return(
                <div>
                    <Card shadow={5} style = {{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style = {{color: '#fff', height: '200px', fontWeight: 'bold',background: 'url(https://www.pngkey.com/png/detail/266-2664932_socrates-for-schools-socrates-cartoon.png) center/cover'}}>
                        </CardTitle>
                        <CardText>
                            <h5 style = {{color: 'black', fontWeight: 'bold'}}>Kontagion Game</h5>
                            The point of this game is to destroy all of the bacteria in the grid by shooring water or throwing flames. You can receive power ups
                            by hovering over them.
                            <br/>
                            <br/>
                            Technologies: C++
                        </CardText>
                        <CardActions border>
                            <Button colored href="https://github.com/sarahmauricio/Kontagion" rel="noopener noreferrer" target = "_blank">GitHub</Button>
                            <Button colored href="https://github.com/sarahmauricio/Kontagion/blob/master/KontagionGame.gif" rel="noopener noreferrer" target = "_blank">Live Demo</Button>
                        </CardActions>
                        <CardMenu style = {{color: '#fff'}}>
                            <IconButton name = "share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        }else if(this.state.activeTab === 3){
            return(
                <div>
                    <Card shadow={5} style = {{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style = {{color: 'black', height: '200px', fontWeight: 'bold',background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRfxF5BqJs-CZw4rJMHPyYxHIdaBZVlZYzZeA&usqp=CAU) center/cover'}}>                       
                        </CardTitle>
                        <CardText>
                            <h5 style = {{color: 'black', fontWeight: 'bold'}}>Tic-Tac-Toe Game</h5>
                            This is a two player game of tic-tac-toe. A list of previous moves is also shown.
                            <br/>
                            <br/>
                            Technologies: JavaScript, React
                        </CardText>
                        <CardActions border>
                            <Button colored href="https://github.com/sarahmauricio/myApp" rel="noopener noreferrer" target = "_blank">GitHub</Button>
                            <Button colored href = "https://github.com/sarahmauricio/myApp/blob/master/tictactoeWalkthrough.gif" rel="noopener noreferrer" target = "_blank">Live Demo</Button>
                        </CardActions>
                        <CardMenu style = {{color: '#fff'}}>
                            <IconButton name = "share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        }
    }

    render(){
        return (
            <div className = "category-tabs">
                <Tabs activeTab = {this.state.activeTab} onChange={(tabId)=>this.setState({activeTab: tabId})} ripple>
                    <Tab>Research Projects</Tab>
                    <Tab>Mobile Applications</Tab>
                    <Tab>Course Projects</Tab>
                    <Tab>Fun</Tab>
                </Tabs>

                
                <Grid>
                    <Cell col = {12}>
                        <div className = "content">{this.toggleCategories()}</div>
                     </Cell>
                </Grid>
                    
                
            </div>
        );
    }
}

export default Projects