import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class Education extends Component{
    render(){
        return(
            <Grid>
                <Cell col = {4}>
                    <p>{this.props.startYear}-{this.props.endYear}</p>
                </Cell>
                <Cell col = {8}>
                    <h4 style={{marginTop: '0px', fontWeight: 'bold', fontSize:'1.8vw', lineHeight: '2.3vw'}}>{this.props.schoolName}</h4>
                    <h5 style = {{fontSize:'1.5vw', lineHeight: '1.9vw'}}>{this.props.schoolDescription}</h5>
                    <p>GPA: {this.props.gpa}</p>
                    <p>Relevant Coursework: {this.props.courses}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Education;