import React, {Component} from 'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';
import Headshot from './Pictures/headshot.JPG'

class Contact extends Component{
    render(){
        return (
            <div className = "contact-body">
                <Grid className = "contact-grid">
                    <Cell col = {4}>
                        <h2 style={{lineHeight: '3.2vw',paddingTop: '.5vw', fontSize: '3vw'}}>Sarah Mauricio</h2>
                        <img src= {Headshot} alt = "my headshot" style={{height:'16vw'}}></img>
                        <p style = {{width:'75%', margin:'auto', paddingTop:'1em'}}>If you have any questions or comments, feel free to contact me !</p>
                    </Cell>
                    <Cell col = {8}>
                        <h2 style={{lineHeight: '3.2vw',paddingTop: '.5vw', fontSize: '3vw'}}>Contact Me</h2>
                        <hr/>

                        <div className = "contact-list">
                        
                        <List>
                            <ListItem>
                                <ListItemContent style ={{paddingLeft: '10vw',fontSize:'2vw', fontFamily:'Anton'}}>
                                    <i style = {{fontSize: '4vw', paddingRight:'1vw'}}className = "fa fa-phone-square" aria-hidden="true"/>
                                    (619) 335-7921
                                </ListItemContent>
                            </ListItem>

                            <ListItem>
                                <ListItemContent style ={{paddingLeft: '10vw',fontSize:'2vw', fontFamily:'Anton'}}>
                                    <i style = {{fontSize: '4vw', paddingRight:'1vw' }}className = "fa fa-envelope" aria-hidden="true"/>
                                    sarahmauricio2019@gmail.com
                                </ListItemContent>
                            </ListItem>
                            
                            <ListItem>
                                <ListItemContent style ={{paddingLeft: '10vw',fontSize:'2vw', fontFamily:'Anton'}}>
                                    <i style = {{fontSize: '4vw', paddingRight:'1vw'}}className = "fa fa-linkedin-square" aria-hidden="true"/>
                                    linkedin.com/in/sarahmauricio/
                                </ListItemContent>
                            </ListItem>
                        </List>

                        </div>
                        
                    </Cell>
                </Grid>
            </div>
        );
    }
}

export default Contact