import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl'

import Headshot from './Pictures/headshot.JPG'

class Landing extends Component{
    render(){
        return (
            <div style = {{width: '100%', margin: 'auto'}}>
                <Grid className = 'landing-grid'>
                    <Cell col = {12}>
                        <img src= {Headshot} alt = "my headshot" className = "avatar-img"></img>
                        <div className = "banner-text">
                            <h1>Aspiring Software Engineer</h1>
                            <hr/>
                            <p>
                                Java | C++ | Python | HTML/CSS | Bootstrap | JavaScript | React
                            </p>
                            <div className = "social-links">
                                {/*LinkedIn*/}
                                <a href="https://www.linkedin.com/in/sarahmauricio/" rel="noopener noreferrer" target = "_blank" >
                                    <i style = {{fontSize: '5vw'}} className="fa fa-linkedin-square" aria-hidden="true"/>
                                </a>

                                {/*Instagram*/}
                                <a href="https://www.instagram.com/sarahhmauricio/" rel="noopener noreferrer" target = "_blank" >
                                    <i style = {{fontSize: '5vw'}}className="fa fa-instagram" aria-hidden="true"/>
                                </a>

                                {/*GitHub*/}
                                <a href="https://github.com/sarahmauricio" rel="noopener noreferrer" target = "_blank" >
                                    <i style = {{fontSize: '5vw'}}className="fa fa-github-square" aria-hidden="true"/>
                                </a>

                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div>
        );
    }
}

export default Landing