import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class Activity extends Component{
    render(){
        return(
            <Grid>
                <Cell col = {4}>
                    <p>{this.props.startYear}-{this.props.endYear}</p>
                    <p>{this.props.year}</p>
                </Cell>
                <Cell col = {8}>
                    <h4 style={{marginTop: '0px', fontWeight: 'bold', fontSize:'1.8vw', lineHeight: '2.3vw'}}>{this.props.activityName}</h4>
                    <h5 style = {{fontSize:'1.5vw', lineHeight: '1.9vw', fontStyle:'italic'}}>{this.props.activityPosition}</h5>
                    <p>{this.props.activityExplanation}</p>
                    <h5 style = {{fontSize:'1.5vw', lineHeight: '1.9vw', fontStyle:'italic'}}>{this.props.activityPosition2}</h5>
                    <p>{this.props.activityExplanation2}</p>
                </Cell>
            </Grid>
        )
    }
}

export default Activity;